body {
  width: 1080px;
  margin: auto;
}
.top-nav {
  height: 50px;
  font-family: "Inria Serif", serif;
  font-size: 2em;
  font-weight: bold;
}
.top-nav div a {
  text-decoration: none;
  color: black;
}
.top-nav div a:visited {
  color: black;
}
.top {
  height: 20px;
}
.topNavContent {
  display: flex;
  justify-content: space-between;
  padding: 0px 20px;
}
.topNavContent > div {
  margin: 4px;
}
.frontImg {
  display: flex;
  position: relative !important;
  justify-content: center;
  z-index: 10;
}
#subnav {
  z-index: 100;
  position: relative;
  list-style: none;
  padding: 0;
  margin-top: 17px;
  background-color: white;
}
.subnavli {
  padding: 10px;
  border: 1px solid;
  border-collapse: collapse;
}

.current {
  color: red !important;
}

.menu a {
  cursor: pointer;
}
.hide {
  display: none;
}
.targetc {
  background: red;
  position: relative;
  width: 13px;
  height: 13px;
  z-index: 11;
}
.view {
  position: relative;
  z-index: 8;
}
.frontBlog {
  height: 300px;
}
.front-content {
  justify-content: center;
  height: 300px;
  display: flex;
}
